@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Inter', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
::-webkit-scrollbar{
  display: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  padding: 0;
  margin:0;
}
.project1-name{display:flex;flex-direction:column;position:absolute;top:10%;left:5%}.project1-name>span{font-weight:600;font-size:2.5rem}.project1-description{display:flex;flex-direction:column;position:absolute;top:30%;left:10%;z-index:1}.project1-description>span{font-size:0.9rem;color:#666}.project1-image1{position:absolute;top:50%;left:-1%}.project1-image1>img{border-radius:10px;height:390px;width:600px}.project1-image2{position:absolute;top:15%;left:46%}.project1-image2>img{border-radius:10px;height:530px;width:880px}

.project2-name{display:flex;flex-direction:column;position:absolute;top:115%;left:68%}.project2-name>span{font-weight:600;font-size:2.5rem}.project2-description{display:flex;flex-direction:column;position:absolute;width:400px;top:130%;left:68%;z-index:1}.project2-description>span{font-size:0.9rem;color:#5a5a5a;margin-bottom:10px}.project2-image1{position:absolute;top:125%;left:-1%}.project2-image1>img{border-radius:14px;height:460px;width:720px}.project2-image2{position:absolute;top:148%;left:61%}.project2-image2>img{border-radius:14px;height:400px;width:660px}

.otherprojects{border-top:1px solid #ccc;position:relative;top:250%;display:flex;flex-direction:column;width:100%;padding-top:40px}.otherprojects-heading{display:flex;flex-direction:column;font-weight:800;margin-left:40px;font-size:2rem}.otherprojects-projects{display:flex;flex-direction:column;align-items:flex-start;margin-left:40%}.otherprojects-projects-dashboard{margin:120px 0px 100px 250px}.otherprojects-projects-dashboard>img{position:absolute;border-radius:10px;height:280px;width:490px;left:10%;top:50%}.otherprojects-projects-dashboard .dashboard-image{opacity:0;-webkit-transform:translateX(-50%);transform:translateX(-50%);visibility:hidden;transition:0.8s}.otherprojects-projects-dashboard>span{font-size:3rem;font-weight:800}.otherprojects-projects-dashboard>span:hover{cursor:pointer}.otherprojects-projects-dashboard:hover .dashboard-image{opacity:1;-webkit-transform:translateX(0%);transform:translateX(0%);visibility:visible}.otherprojects-projects-cardeko{margin-left:250px;margin-bottom:100px}.otherprojects-projects-cardeko>span{margin-bottom:14px;font-size:3rem;font-weight:800}.otherprojects-projects-cardeko>span:hover{cursor:pointer}.otherprojects-projects-cardeko .cardeko-image{opacity:0;-webkit-transform:translateX(-50%);transform:translateX(-50%);visibility:hidden;transition:0.8s}.otherprojects-projects-cardeko>img{position:absolute;border-radius:10px;height:600px;width:360px;left:18%;top:28%}.otherprojects-projects-cardeko:hover .cardeko-image{opacity:1;-webkit-transform:translateX(0%);transform:translateX(0%);visibility:visible}.otherprojects-projects-crud{margin-left:250px}.otherprojects-projects-crud>img{visibility:hidden;position:absolute;border-radius:10px;height:280px;width:490px;left:10%;top:50%}.otherprojects-projects-crud .crud-image{opacity:0;-webkit-transform:translateX(-50%);transform:translateX(-50%);visibility:hidden;transition:0.8s}.otherprojects-projects-crud>span{margin-top:14px;font-size:3rem;font-weight:800}.otherprojects-projects-crud>span:hover{cursor:pointer}.otherprojects-projects-crud:hover .crud-image{opacity:1;-webkit-transform:translateX(0%);transform:translateX(0%);visibility:visible}

.contact a{text-decoration:none}.contact-top{width:100%;height:20vh;background-color:black}.contact-top-links{display:flex;flex-direction:row;height:100%;align-items:center;justify-content:space-between}.contact-top-links-left{margin-left:20px;display:flex;flex-direction:row;justify-content:space-between;width:250px}.contact-top-links-left .LinkedIn>a,.contact-top-links-left .Github>a{font-weight:600;color:#5249a8;font-size:1.3rem}.contact-top-links-right{margin-right:40px}.contact-top-links-right .contact-mail>span{color:#a0a0a0;font-weight:800}.contact-top-links-right .contact-mailId{margin-top:8px}.contact-top-links-right .contact-mailId a{color:#7c7c7c;font-size:0.8rem;font-weight:500}.contact-bottom{width:100%;height:6vh;background-color:#181818;display:flex;justify-content:flex-start;align-items:center}.contact-bottom .copyright>span{font-weight:600;margin-left:20px;font-size:0.8rem;color:#8d8d8d}

.navbar{box-sizing:border-box;padding:30px 30px 40px;width:100%;height:10vh;-webkit-animation:fromUp 2s;animation:fromUp 2s}.navbar-container{display:flex;flex-direction:row;justify-content:space-between}.navbar-left{width:50%;display:flex;flex-direction:row;align-items:center}.navbar-left-logo{display:flex;flex-direction:column;align-items:center}.navbar-left-logo>span{margin-left:40px;font-size:1.6rem;font-weight:600;-webkit-transform:rotate(0deg);transform:rotate(0deg);transition:-webkit-transform 800ms;transition:transform 800ms;transition:transform 800ms, -webkit-transform 800ms}.navbar-left-logo>span:hover,.navbar-left-logo>span:active{cursor:pointer;-webkit-transform:rotate(360deg);transform:rotate(360deg)}.navbar-right{width:50%;display:flex;margin-top:5px;align-items:center;justify-content:flex-end}.navbar-right-projects,.navbar-right-about,.navbar-right-contact{display:flex;flex-direction:row;align-items:center;margin-right:100px;border-bottom:2px solid transparent;height:100%;transition:border 600ms}.navbar-right-projects>span,.navbar-right-about>span,.navbar-right-contact>span{font-size:1rem;font-weight:600}.navbar-right-projects:hover,.navbar-right-about:hover,.navbar-right-contact:hover{cursor:pointer;border-bottom:2px solid #646464}@-webkit-keyframes fromUp{from{-webkit-transform:translate(0, -100%);transform:translate(0, -100%);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}@keyframes fromUp{from{-webkit-transform:translate(0, -100%);transform:translate(0, -100%);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}

.main{box-sizing:border-box}.main-container{display:flex;flex-direction:row;height:90vh}.main-left{width:60%;padding-top:180px;padding-left:150px;-webkit-animation:fromLeft 2s;animation:fromLeft 2s}.main-left-roles{color:#636363;padding:0;margin:0;font-size:1rem;font-weight:600}.main-left-name{color:#303030}.main-left-name-first,.main-left-name-last{padding:0;margin:0}.main-left-name-first>span,.main-left-name-last>span{font-size:5.2rem;font-weight:500}.main-left-name-last{margin-top:-14px}.main-right{width:50%;margin-top:12px;margin-right:150px}@-webkit-keyframes fromLeft{from{-webkit-transform:translate(-100%, 0);transform:translate(-100%, 0);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}@keyframes fromLeft{from{-webkit-transform:translate(-100%, 0);transform:translate(-100%, 0);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}@-webkit-keyframes fromRight{from{-webkit-transform:translate(150%, 0);transform:translate(150%, 0);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}@keyframes fromRight{from{-webkit-transform:translate(150%, 0);transform:translate(150%, 0);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}@-webkit-keyframes fromDown{from{-webkit-transform:translate(0, 100%);transform:translate(0, 100%);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}@keyframes fromDown{from{-webkit-transform:translate(0, 100%);transform:translate(0, 100%);opacity:0}to{-webkit-transform:translate(0%, 0);transform:translate(0%, 0);opacity:1}}@-webkit-keyframes visibleOp{from{opacity:0}to{opacity:1}}@keyframes visibleOp{from{opacity:0}to{opacity:1}}

.App .Home{background-color:white}.App .allProjects{background-color:#F3F3F3}

